<template>
  <div class="register">

    <!-- NarBar  -->
    <van-nav-bar
      title="注册"
      left-text="登录"
      right-text="关闭"
      @click-left="getgo('Login')"
      @click-right="getgo('Menu')"
    />
    <div class="logo">
      <img class="auto-img" src="../assets/images/logo.jpg" alt="">
    </div>
    <!-- From + Field  -->
    <van-form @submit="register">
      <van-field
        v-model="userinfo.phone"
        type="text"
        label="手机号"
        placeholder="手机号"
        left-icon="phone-o"
        maxlength="11"
      >
        <!-- 自定义右边图标 -->
        <template v-slot:right-icon>
          <i class="fa fa-camera-retro fa-lg"></i>
        </template>
        
      </van-field>
      <van-field
        v-model="userinfo.nickName"
        type="text"
        label="昵称"
        placeholder="昵称"
        left-icon="contact"
        maxlength="10"
      />
      <van-field
        v-model="userinfo.password"
        :type="iseye ? 'text' : 'password'"
        label="密码"
        placeholder="密码"
        left-icon="lock"
        :right-icon="iseye ? 'eye-o' : 'closed-eye'"
        @click-right-icon = passwords
        maxlength="16"
      />
      <div class="register-btn">
        <van-button round block type="info" native-type="submit">立即注册</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>

  //导入样式
  import "../assets/css/register.less"
    //导入验证表单模块
  import validFrom from "../assets/js/validFrom"

  export default {
      data(){
        return{

          //用户注册信息
          userinfo:{
            phone:"",
            nickName:"",
            password:""
          },
          //false: 闭合，true：睁开  密码
          iseye:false
        }
      },
      // created(){
      //   this.$nextTick(()=>{
      //     let eye = document.querySelector(".van-icon-closed-eye")
      //   })
      // },
      methods:{

        //登录跳转
        getgo(name){
          this.$router.push({name})
        },

        //切换密码的格式 自定义事件
        passwords(){
          this.iseye = !this.iseye
        },

        //注册
        register(){

          if (validFrom.validUserFrom(this.userinfo)) {
              //复制对象
              let data = Object.assign({}, this.userinfo);
              //请求密钥
              data.appkey = this.appkey;

              //开启加载提示
              this.$toast.loading({
                message: '加载中...',
                forbidClick: true,

                //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                duration:0
              });

              //发起注册请求
              this.axios({
                method: 'POST',
                url: '/register',

                //post传递参数需要写在data
                data

              }).then(result => {
                //关闭加载提示
                this.$toast.clear();

                

                //判断手机号是否已经被注册
                if(result.data.code == 100){
                  //注册成功 跳转到登录页面
                  this.$router.push({name: 'Login'});

                }else{
                  this.$toast(result.data.msg)
                }
              }).catch(err => {
                //关闭加载提示
                this.$toast.clear();
                
              })



          }
        }
      }
  }
</script>